import { useRouter } from 'next/router';
import { useEffect } from 'react';
import { useTranslation } from '@fanfanlo';
import { AppbarContainer, AppShell } from '@app/ui';
import { MemberDetailContent } from '@src/im/components/room-settings/member-detail/MemberDetailContent';

export default function MemberDetailPage() {
  const { t } = useTranslation('homepage/pages/im/room/room-settings/member-detail/content');
  const router = useRouter();
  const { roomId, userId } = router.query;

  useEffect(() => {
    if ((!roomId || !userId) && router.isReady) {
      router.push('/im');
    }
  }, [roomId, userId, router]);

  return (
    <AppShell>
      <AppbarContainer appbarProps={{ title: t('content.title'), back: true }}>
        {roomId && userId && (
          <MemberDetailContent roomId={roomId as string} userId={userId as string} />
        )}
      </AppbarContainer>
    </AppShell>
  );
}
